.library {
    position: fixed;
    top: 0;
    left: 0;
    width: 20rem;
    height: 100%;
    background: lightcyan;
    box-shadow: 2px 2px 50px gray;
    overflow: scroll;
    transform: translateX(-100%);
    transition: all 0.5s ease;
    opacity: 0;
    h2 {
        padding: 2rem;
    }
}

.library-song {
    display: flex;
    align-items: center;
    padding: 1rem 2rem 1rem 2rem;
    cursor: pointer;
    img{
        width: 30%
    }
    &:hover{
        background: lightblue;
    }
}

.song-description {
    padding-left: 1rem;
    h3{
        font-size: 1rem;
    }
    h4{
        font-size: 0.7rem;
    }
}

.selected{
    background: violet;
}

.active-Library {
    transform: translateX(0%);
    opacity: 1;
}
*{
    scrollbar-witdh: thin;
    scrollbar-color: rgba(155, 155, 155, 0.5) transparent;
}
